body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#singleBlock {
  display:inline-block;
  width:18px;
  height:22px;
  border-radius: 1px;;
  font-family:'Orbitron', sans-serif;
  vertical-align: middle;
}

#singleBlock div {
  background-image: url(data:image/jpeg;base64,);
  display:inline-block;
  width:18px;
  height:22px;
  border-radius: 1px;;
  font-family:'Orbitron', sans-serif;
  display: table-cell;
  vertical-align: middle;
}

.turnPaper {
  -webkit-animation: turnPaper-spin 200ms linear;
          animation: turnPaper-spin 200ms linear; 
}

.reversePaper {
  -webkit-animation: reversePaper-spin 200ms linear;
          animation: reversePaper-spin 200ms linear;
}

.suffix {
  vertical-align: middle;
  font-family: 'Anton', sans-serif;
  font-size: 80%;
}

.eventInfo {
  font-family: 'Press Start 2P', cursive;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes turnPaper-spin {
  0%   {-webkit-transform: scale(1,1);transform: scale(1,1);}
  25%  {-webkit-transform: scale(1,0.5);transform: scale(1,0.5);}
  50%  {-webkit-transform: scale(1,0);transform: scale(1,0);}
  60%  {-webkit-transform: scale(1,0.5);transform: scale(1,0.5);}
  70%  {-webkit-transform: scale(1,1);transform: scale(1,1);}
  85%  {-webkit-transform: scale(1,0.7);transform: scale(1,0.7);}
  100% {-webkit-transform: scale(1,1);transform: scale(1,1);}
}

@keyframes turnPaper-spin {
  0%   {-webkit-transform: scale(1,1);transform: scale(1,1);}
  25%  {-webkit-transform: scale(1,0.5);transform: scale(1,0.5);}
  50%  {-webkit-transform: scale(1,0);transform: scale(1,0);}
  60%  {-webkit-transform: scale(1,0.5);transform: scale(1,0.5);}
  70%  {-webkit-transform: scale(1,1);transform: scale(1,1);}
  85%  {-webkit-transform: scale(1,0.7);transform: scale(1,0.7);}
  100% {-webkit-transform: scale(1,1);transform: scale(1,1);}
}

@-webkit-keyframes reversePaper-spin {
  0%   {-webkit-transform: scale(1,1);transform: scale(1,1);}
  25%  {-webkit-transform: scale(1,0.5);transform: scale(1,0.5);}
  50%  {-webkit-transform: scale(1,0);transform: scale(1,0);}
  60%  {-webkit-transform: scale(1,0.5);transform: scale(1,0.5);}
  70%  {-webkit-transform: scale(1,1);transform: scale(1,1);}
  85%  {-webkit-transform: scale(1,0.7);transform: scale(1,0.7);}
  100% {-webkit-transform: scale(1,1);transform: scale(1,1);}
}

@keyframes reversePaper-spin {
  0%   {-webkit-transform: scale(1,1);transform: scale(1,1);}
  25%  {-webkit-transform: scale(1,0.5);transform: scale(1,0.5);}
  50%  {-webkit-transform: scale(1,0);transform: scale(1,0);}
  60%  {-webkit-transform: scale(1,0.5);transform: scale(1,0.5);}
  70%  {-webkit-transform: scale(1,1);transform: scale(1,1);}
  85%  {-webkit-transform: scale(1,0.7);transform: scale(1,0.7);}
  100% {-webkit-transform: scale(1,1);transform: scale(1,1);}
}
